* {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

body {
  padding: 60px;
  font-size: 1.5rem;
}

input {
  color: gray;
  font-size: 1.2rem;
  width: 100%;
}

.card {
  align-content: center;
  border-radius: 12px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  width: 200px;
}

.card-grid {
  display: flex;
  flex-direction:  row;
  flex-wrap: wrap;
}

.card p {
  padding: 5px;
  text-align: center;
  font-size: 1rem;
}

.card img {
  height: 150px;
  width: 100%;
  object-fit: cover;
}
